    letter-spacing: 1.43px;
    display: block;
    width: 200px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }

  .cardText span {
    font-weight: lighter;
  }

  .btnSearchContainer {
    width: 75%;
    background: gray;
    border-radius: 8px;
  }
  .btnModalSearch {
    width: 100%;
    height: 50px;
    position: relative;
    text-align: center;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    background: linear-gradient(to top, orange, rgb(255, 145, 0));
    color: white;
    border: none;
    border-radius: 8px;
    font-family: "Josefin Sans", sans-serif;
    font-size: 1rem;
    font-weight: 900;
    letter-spacing: 2px;
    text-transform: uppercase;
    cursor: pointer;
    display: inline-block;
    transition: all 0.4s;
    z-index: 0;
  }

  .btnModalSearch:hover {
    background: rgb(33, 112, 37);
    z-index: 10;
  }
}


