.collectionsContainer {
  display: flex;
  flex-direction: column;
  max-width: 100%;
  height: 100%;
  overflow-x: hidden;
  min-height: 100vh;
  background: linear-gradient(to top, rgb(11, 11, 17), #15171c);
  color: white;
  padding-top: 0px;
}

.collectionBackground {
  max-height: 600px;
  object-fit: cover;
  mask-image: linear-gradient(
    to bottom,
    rgba(11, 11, 17, 1),
    rgba(11, 11, 17, 0)
  );
}

.collectionsTopContainer {
  width: 100%;
  height: 150px;
  display: flex;
  justify-content: space-between;
  margin-left: 15px;
  margin-top: -160px;
  padding: 20px;
  letter-spacing: 1.42px;
}

.collectionsTopContainer h1 {
  display: flex;
  color: white;
  font-size: 1.8rem;
  margin-bottom: 25px;
  text-transform: capitalize;
  z-index: 40;
}

.collectionsSortContainer {
  display: flex;
}

.collectionsBtn {
  margin-right: 28px;
  cursor: pointer;
}

.collectionsBtn span {
  position: relative;
}

.collectionsBtn span::after {
  content: "";
  height: 2px;
  background: white;
  position: absolute;
  left: 0;
  right: 0;
  bottom: -5px;
  opacity: 0;
  transition: all 250ms cubic-bezier(0.25, 0.46, 0.45, 0.94) 0s;
  transform: scaleX(0);
}

.collectionsBtn span:hover::after {
  transform: scaleX(1);
  opacity: 1;
}

.collectionsImageContainer {
  width: 50%;
  height: 390px;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
}

.collectionsImage {
  height: 95%;
  object-fit: contain;
  margin-bottom: 200px;
  margin-right: -67px;
  z-index: 1;
}

.collectionsBottomContainer {
  display: flex;
  width: 100%;
  height: 100%;
}

.collectionMoviesContainer {
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns: repeat(7, 10%);
  grid-column-gap: 3.33%;
  grid-row-gap: 60px;
  padding-bottom: 100px;
  margin-left: 17px;
  margin-top: -20px;
}

.collectionsRowMovie {
  width: 450px;
  height: 200px;
  align-items: center;
  justify-content: center;
  padding-bottom: 30px;
  padding-top: 10px;
}

.collectionsMoviePoster {
  height: 195px;
  max-width: 140px;
  object-fit: cover;
  border-radius: 4px;
  border: 1px solid gray;
  transition: transform 450ms;
}

.collectionsMoviePoster:hover {
  transform: scale(1.2);
}

@media (max-width: 768px) {
  .collectionsTopContainer {
    height: 100px;
    margin-left: 0px;
    margin-top: -60px;
    padding: 12px;
    letter-spacing: 1.42px;
  }

  .collectionsTopContainer h1 {
    font-size: 1.2rem;
    margin-bottom: 5px;
  }

  .collectionsImageContainer {
    display: none;
  }

  .collectionsBtn {
    font-size: 12px;
    margin-right: 12px;
    margin-top: 5px;
    cursor: none;
  }

  .collectionMoviesContainer {
    width: 100%;
    height: 100%;
    display: grid;
    grid-template-columns: repeat(3, 30%);
    grid-column-gap: 1.33%;
    grid-row-gap: 0px;
    padding-bottom: 0px;
    margin-left: 0px;
    margin-top: -10px;
  }

  .collectionsRowMovie {
    width: 100%;
    height: 180px;
    padding-bottom: 0px;
    padding-top: 0px;
  }

  .collectionsMoviePoster {
    height: 150px;
    max-width: 120px;
    transition: transform 450ms;
  }

  .collectionsMoviePoster:hover {
    transform: scale(1);
  }
}
