@import url("https://fonts.googleapis.com/css2?family=Goblin+One&display=swap");

.row {
  margin-top: 10px;
  margin-left: 20px;
  margin-right: 20px;
}

.row h1 {
  display: flex;
  color: white;
  font-size: 1.8rem;
  letter-spacing: 1.42px;
  margin-bottom: 5px;
  margin-left: 15px;
}

.row_movies {
  display: flex;
  min-height: 200px;
  overflow-y: hidden;
  overflow-x: scroll;
  padding-top: 5px;
  padding-bottom: 50px;
  margin-bottom: 5px;
}

.row_movies::-webkit-scrollbar {
  display: none;
}

.row_movie {
  width: 450px;
  height: 200px;
  margin-right: -20px;
  align-items: center;
  justify-content: center;
  padding-bottom: 30px;
  padding-top: 10px;
  transition: transform 450ms;
}

.row_movie:hover {
  transform: scale(1.2);
}

.movie_poster {
  height: 200px;
  min-width: 100px;
  object-fit: contain;
  border-radius: 4px;
  border: 1px solid gray;
}

.buttonContainer {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.rowOne,
.rowTwo,
.rowThree,
.rowFour,
.rowFive,
.rowSix,
.rowSeven,
.rowEight {
  color: rgba(255, 255, 255, 0.527);
  text-decoration: none;
  font-size: 3em;
  background: rgb(11, 11, 17);
  border: none;
  text-align: center;
}

.left-arrow {
  position: absolute;
  left: 0;
  top: 100%;
  height: 75px;
  width: 50px;
  margin-top: 80px;
  font-family: "Goblin One", cursive;
  background: rgba(58, 57, 57, 0.7);
  border-bottom-right-radius: 510px;
  border-top-right-radius: 510px;
  z-index: 1;
}

.right-arrow {
  position: absolute;
  right: 0;
  top: 100%;
  height: 75px;
  width: 50px;
  margin-top: 80px;
  font-family: "Goblin One", cursive;
  background: rgba(58, 57, 57, 0.7);
  border-bottom-left-radius: 510px;
  border-top-left-radius: 510px;
  z-index: 1;
}

.right-arrow:hover,
.left-arrow:hover {
  color: white;
}

.right-arrow:focus .left-arrow:focus {
  outline: none !important;
  outline-offset: none !important;
}

@media (max-width: 768px) {
  .right-arrow {
    display: none;
  }

  .left-arrow {
    display: none;
  }

  .row {
    margin-left: 12px;
    margin-top: 0px;
  }

  .row h1 {
    font-size: 1.2rem;
    letter-spacing: 1.42px;
    margin-left: 12px;
  }

  .row_movies {
    min-height: 150px;
    padding-bottom: 25px;
    overflow-y: hidden;
    overflow-x: scroll;
  }

  .row_movie {
    width: 100%;
    height: 150px;
    margin-right: -20px;
  }

  .row_movie:hover {
    transform: scale(1);
  }

  .movie_poster {
    height: 150px;
    min-width: 100%;
    object-fit: contain;
    border-radius: 4px;
    border: 1px solid gray;
  }
}
