@import url("https://fonts.googleapis.com/css2?family=Lato&display=swap");

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Lato", sans-serif;
}

.home,
.reports,
.products,
.team,
.reports {
  display: flex;
  height: 90vh;
  align-items: center;
  justify-content: center;
  font-size: 3rem;
}

.input {
  display: flex;
  border: 5px black;
  align-items: center;
  justify-content: center;
  padding: auto;
  width: 200px;
  font-size: large;
  text-align: center;
  
  margin-top: 20px;
}
